.profile {
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 5px 0 0 0;
  color: #ffffff;
  font-size: 14px;
  font-family: inherit;
  background-color: #7f7d86;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.name,
.tag,
.location {
  font-family: 'Times New Roman', Times, serif;
  font-size: 20px;
}
.stats {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  list-style: none;
}

.label {
  padding: 10px;
  font-family: serif;
}

.quantity {
  color: black;
  font-weight: bold;
}
