.statistics {
  width: 300px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
}

.title {
  display: flex;
  justify-content: center;
}

.statList {
  display: flex;
  justify-content: center;
  list-style: none;
  margin: 0 auto;
}
.item {
  display: flex;
  flex-wrap: wrap;
  padding: 5px;
}
